<template>
  <div class="AdminAddSupplier">
    <el-form
      label-position="right"
      :rules="rules"
      ref="supplierFrom"
      label-width="100px"
      :model="supplierFrom"
    >
      <el-form-item label="" prop="supplierName">
        <el-input v-model="supplierFrom.supplierName"></el-input>
      </el-form-item>

      <el-form-item>
        <el-button type="primary" @click="submitForm('supplierFrom')"
          ></el-button
        >
        <el-button @click="resetForm('supplierFrom')"></el-button>
        <el-button @click="jumpGo()"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
// axios
import request from '@/utils/request.js'

export default {
  data() {
    return {
      name: 'AdminAddGoods',
      //   from
      supplierFrom: {
        supplierName: ''
      },
      //   from
      rules: {
        supplierName: [{ required: true, message: '', trigger: 'blur' }]
      }
    }
  },
  methods: {
    // 
    submitForm(supplierFrom) {
      this.$refs[supplierFrom].validate((valid) => {
        if (valid) {
          request.post('/addSupplierOne', this.supplierFrom)
          alert('')
          this.$router.replace('/jumpAdmin/jumpAdminSupplier')
        } else {
          alert('')
          return false
        }
      })
    },
    // 
    resetForm(supplierFrom) {
      this.$refs[supplierFrom].resetFields()
    },
    // 
    jumpGo() {
      this.$router.replace('/jumpAdmin/jumpAdminSupplier')
    }
  }
}
</script>
<style lang="less" scoped>
.AdminAddSupplier {
  width: 500px;
  margin: 200px auto;
}
</style>
